        <el-form-item label="" prop="reminder">
          <el-select v-model="calendarEvent.reminder" placeholder="">
            <el-option label="" value="none"></el-option>
            <el-option label="10" value="10min"></el-option>
            <el-option label="30" value="30min"></el-option>
            <el-option label="1" value="1hour"></el-option>
