              if (avatar.startsWith('http://') || avatar.startsWith('https://')) {
                avatar_url = avatar;
              } else if (avatar.startsWith('/')) {
                avatar_url = `${process.env.VUE_APP_BASE_API || 'http://localhost:8000'}${avatar}`;
              } else {
                avatar_url = `${process.env.VUE_APP_BASE_API || 'http://localhost:8000'}/${avatar}`;
              }
            }
            
            tempContact = {
              id: parseInt(chatId),
              name: (user.name || (((user.first_name || '') + ' ' + (user.last_name || '')).trim() || user.username) || '').trim(),
              initial: ((user.name || user.first_name || user.username || '')[0]),
