    const fileUploading = ref(false)
    const imageUploading = ref(false)
    
    // 
    const handleFileUpload = async (event) => {
      const file = event.target.files[0]
      if (!file || !currentChatId.value) return
      try {
        // 
        fileUploading.value = true
        
        // sendFileMessage
        await sendFileMessage(
          currentChatId.value,
          `: ${file.name}`,
          file
        )
        
        // AI
        const formData = new FormData()
        formData.append('file', file)
        formData.append('chat', currentChatId.value)
        await request({
          url: '/api/ai/documents/upload/',
          method: 'post',
          data: formData,
          headers: { 'Content-Type': 'multipart/form-data' }
        })
        
        // ID
        const docRes = await request({
          url: '/api/ai/documents/',
          method: 'get',
          params: { chat: currentChatId.value }
        })
        if (docRes && docRes.data && Array.isArray(docRes.data.results)) {
          selectedDocumentIds.value = docRes.data.results.map(doc => doc.id)
          console.log('IDs:', selectedDocumentIds.value)
        }
        
        ElMessage.success('')
        
        // 
        loadChatMessages(currentChatId.value, true)
        event.target.value = ''
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      } finally {
        // 
        fileUploading.value = false
      }
    }
    
    // 
    const handleImageUpload = async (event) => {
      const file = event.target.files[0]
      if (!file || !currentChatId.value) return
      
      try {
        // 
        if (!file.type.startsWith('image/')) {
          ElMessage.error('')
          event.target.value = ''
          return
        }
        
        // 10MB
        const maxSize = 10 * 1024 * 1024 // 10MB
        if (file.size > maxSize) {
          ElMessage.error('10MB')
          event.target.value = ''
          return
        }
        
        // 
        imageUploading.value = true
        
        console.log('ID:', currentChatId.value, ':', file.name, ':', file.size)
        
        // API
        await sendImageMessage(
          currentChatId.value, 
          ``, 
          file
        )
        
        // 
        loadChatMessages(currentChatId.value, true)
        
        // 
        event.target.value = ''
      } catch (error) {
        console.error(':', error)
        
        // 
        let errorMessage = ''
