/**
 * 
 * @param {Number} messageId - ID
 */
export function downloadMessageFile(messageId) {
  return request({
    url: `/api/chat/messages/${messageId}/download/`,
    method: 'get',
    responseType: 'blob'
  })
}

/**
 *  - 
 * @param {Object} data - chat(ID)document_id(ID)format(pdf/word)title()
 */
export function sendDocumentMessage(data) {
  // FormData
  const formData = new FormData();
  
  // 
  formData.append('chat', data.chat);
  formData.append('message_type', 'file');
  formData.append('document_id', data.document_id);
  formData.append('format', data.format);
  formData.append('title', data.title);
  formData.append('content', `${data.format === 'pdf' ? 'PDF' : 'Word'}`);
  
  return request({
    url: '/api/chat/messages/',
    method: 'post',
    data: formData,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

