/**
 * 
 * @param {Number} messageId - ID
 * @param {Object} data - 
 * @param {Boolean} preview - 
 */
export function addCalendarEvent(messageId, data, preview = false) {
  let url = `/api/chat/messages/${messageId}/add_calendar_event/`
  if (preview) {
    url += '?preview=true'
  }
  
  return request({
    url: url,
    method: 'post',
    data
  })
}

